<template>
  <div>
    <!-- 月份 -->
    <div>
      <mt-navbar
        v-model="selected"
        @click.native="chooseSelected"
      >
        <mt-tab-item id="1">
          <span>待处理审批</span>
        </mt-tab-item>
        <mt-tab-item id="2">
          <span>已处理审批</span>
        </mt-tab-item>
      </mt-navbar>
      <!-- 申请列表 -->
      <div
        v-if="applyList.length > 0"
        class="scroll"
      >
        <van-pull-refresh
          v-model="pullLoading"
          @refresh="onPullRefresh"
          success-text="刷新成功"
        >
          <van-list
            v-model="listLoading"
            @load="onLoadList"
            :offset="30"
            :finished="finishedList"
            finished-text="没有更多了"
          >
            <div
              v-for="(item, i) in applyList"
              :key="i"
              @click="goNext('edit-or-see-apply',item)"
              style="background:#fff;padding:10px 0;margin-bottom:10px;"
            >
              <div
                class="info-hotel"
                v-if="item.hotel"
              >
                <div class="hotel-left">
                  <div style="width:80px;margin-right:8px;">
                    <img
                      :src="item.hotel.logo"
                      class='left-img'
                    >
                  </div>
                  <div class="left-title">
                    <div style="display:flex;align-items:center;">
                      <div class="c_000">{{ item.hotel.hotelName }}</div>
                      <div
                        v-if="auditApprovalType"
                        style="font-size:12px;border:1px solid #E5E2E5;padding:2px;margin-left:8px;"
                      >{{ auditApprovalType[item.applyType || item.auditApproval.applyType] }}</div>
                    </div>
                    <div class="adr">{{ item.hotel.location || '暂无' }}</div>
                    <div style="font-size:14px;color:#888;">{{ item.hotel.contacts || '暂无' }}
                    </div>
                  </div>
                </div>
                <div
                  v-if="selected == '1'"
                  class="hotel-right"
                >
                  <div :style="{color:(item.status == '0' || item.status == '2'?'#ff7d41':'red')}">{{ statusAll[item.status] }}</div>
                </div>
                <div
                  v-else
                  class="hotel-right"
                >
                  <div :style="{color:(item.auditApproval.status == '0' || item.auditApproval.status == '2'?'#ff7d41':'red')}">{{ statusAll[item.auditApproval.status] }}</div>
                </div>
              </div>
              <div style="display:flex;justify-content:flex-end;">
                <div
                  v-if="selected == '1' && item.role == 'ROLE_AREA_GM' && item.status !='1' || item.role == 'ROLE_AREA_MANAGER'  && item.status !='1'"
                  class="edit-reason"
                  @click.stop="btnReject(item)"
                >驳回申请</div>
                <div
                  v-if="item.role == 'ROLE_AREA_MANAGER'"
                  class="edit-reason"
                  @click.stop="goNext(item.applyType == '2' ?'edit-or-see-apply':'add-equipment',item)"
                >{{ item.applyType == '2'?'查看申请':'线下查验' }}</div>
                <div
                  v-if="selected == '1' && item.role == 'ROLE_AREA_GM'"
                  class="edit-reason"
                  @click.stop="passApply(item)"
                >通过申请</div>
              </div>
            </div>
          </van-list>
        </van-pull-refresh>
      </div>
      <div
        v-else
        class="nothing"
      >
        <img
          src="@/assets/hotelAudit/empty.png"
          alt=""
        >
        <div>没有待审核的酒店</div>
      </div>
    </div>
    <!-- 驳回理由 -->
    <van-dialog
      v-model="showReject"
      show-cancel-button
      :before-close="confirmReject"
    >
      <van-field
        v-model="auditFailReason"
        rows="3"
        type="textarea"
        placeholder="请简述驳回原因"
      />
    </van-dialog>
  </div>
</template>
<script>
import { GetSystemDictDataByType } from '@/api/api_query';
import { GetBusinessAuditApprovalApprovaledList, GetBusinessAuditApprovalByroleList, PostBusinessAuditApprovalRefuse, PostBusinessAuditApprovalCommit } from '@/api/api_equip';
import { Dialog, Toast } from 'vant';
export default {
    components: {},
    data() {
        return {
            listLoading: false, // 上滑列表加载（每一次上滑的时候）---加载中
            finishedList: false, // 上滑列表加载完了所有数据---没有更多了）
            pullLoading: false, // 下拉刷新加载动画---加载中
            showReject: false,
            showPay: false,
            applyList: [], // 列表数据
            auditApprovalType: {},
            statusAll: { 0: '申请中', 1: '被驳回', 2: '申请通过', 3: '已取消' },
            dataForm: {
                pageNum: 0,
                pageSize: 10
            },
            auditFailReason: null,
            selected: '1',
            roles: []
        };
    },
    filters: {
        filterAmount(price) {
            return price.toFixed(2);
        }
    },
    created() {
        this.$store.commit('setHeader', '申请列表');
        this.getType();
    },
    methods: {
        getType() {
            GetSystemDictDataByType({ dictType: 'business_audit_approval_type' }).done((res) => {
                res.data.map((item) => {
                    this.auditApprovalType[item.dictValue] = item.dictLabel;
                });
                this.onLoadList();
            });
        },
        // 选择清洁，保洁
        chooseSelected() {
            this.applyList = [];
            this.dataForm.pageNum = 0;
            this.onLoadList();
        },
        //  下拉刷新列表
        onPullRefresh() {
            this.applyList = [];
            this.dataForm.pageNum = 0;
            this.finishedList = false;
            this.onLoadList();
        },
        //  上滑加载列表
        onLoadList() {
            if (this.selected == 1) {
                GetBusinessAuditApprovalByroleList(this.dataForm).done((res) => {
                    this.applyList = this.applyList.concat(res.data);
                    this.listLoading = this.pullLoading = false;
                    if (res.total == this.applyList.length) {
                        this.finishedList = true;
                    } else if (res.total > this.applyList.length) {
                        this.finishedList = false;
                    }
                });
                this.dataForm.pageNum++;
            } else {
                GetBusinessAuditApprovalApprovaledList(this.dataForm).done((res) => {
                    this.applyList = this.applyList.concat(res.data);
                    this.listLoading = this.pullLoading = false;
                    if (res.total == this.applyList.length) {
                        this.finishedList = true;
                    } else if (res.total > this.applyList.length) {
                        this.finishedList = false;
                    }
                });
                this.dataForm.pageNum++;
            }
        },
        // 驳回-1
        btnReject(item) {
            this.showReject = true;
            this.rejectInfo = item;
        },
        // 驳回-2
        confirmReject(action, done) {
            if (action == 'confirm') {
                if (!this.auditFailReason) {
                    Toast('请输入驳回原因');
                    done(false);
                    return;
                }
                let data = {
                    auditApprovalId: this.rejectInfo.id,
                    auditFailReason: this.auditFailReason
                };
                PostBusinessAuditApprovalRefuse(data)
                    .done((res) => {
                        Toast.success(res.msg);
                        this.showReject = false;
                        done(false);
                        this.getType();
                        setTimeout(() => {
                            this.$router.go(0);
                        }, 900);
                    })
                    .then((e) => {
                        this.showReject = false;
                        this.auditFailReason = null;
                        done(false);
                    });
            } else if (action == 'cancel') {
                done(false);
                this.auditFailReason = null;
                this.showReject = false;
            }
        },
        // 通过申请
        passApply(item) {
            Dialog.confirm({
                title: '通过申请'
            })
                .then(() => {
                    PostBusinessAuditApprovalCommit({ auditApprovalId: item.id }).done((res) => {
                        Toast.success(res.msg);
                        this.getType();
                        setTimeout(() => {
                            this.$router.go(0);
                        }, 900);
                    });
                })
                .catch(() => {
                    console.log('不通过');
                });
        },
        // 申请详情
        goNext(url, item) {
            if (this.selected == '2') {
                this.$router.push({
                    path: url,
                    query: {
                        auditApprovalId: item.auditApprovalId,
                        applyType: item.auditApproval.applyType
                    }
                });
            } else {
                this.$router.push({
                    path: url,
                    query: {
                        auditApprovalId: item.id,
                        hotelId: this.$route.query.hotelId,
                        applyHotelId: item.hotelId,
                        role: item.role,
                        applyType: item.applyType
                    }
                });
            }
        }
    }
};
</script>
<style scoped>
.scroll {
    margin: 10px 0;
    overflow: auto;
    position: fixed;
    bottom: 0;
    top: 100px;
    right: 0;
    left: 0;
}
.nothing {
    margin: 100px auto;
    line-height: 50px;
    font-size: 15px;
}
.info-hotel {
    font-size:15px;
    display: flex;
    justify-content: space-between;
    padding: 0 10px 20px 10px;
    color: #b2b2b2;
}
.hotel-left {
    display: flex;
    align-items: center;
    text-align: left;
    width: 80%;
}
.left-img {
    width: 80px;
    height: 80px;
    border-radius: 4px;
}
.left-title {
    height: 86px;
}
.hotel-right {
    display: flex;
}
/* 驳回原因 */
.reason {
    color: #000;
    font-size: 14px;
    text-align: left;
    padding: 10px;
    line-height: 20px;
}
.reason-right {
    display: flex;
    justify-content: space-between;
    text-align: center;
}
.edit-reason {
    padding: 4px 8px;
    border: 1px solid #ff7d41;
    border-radius: 2px;
    color: #ff7d41;
    font-size: 12px;
    margin: 0 6px;
}
.c_000 {
    color: #000;
    font-size: 16px;
    max-width: 90px;
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.c_f60 {
    color: #ff7d41;
}

.c_ce {
    color: #cecece;
}
.adr {
    font-size: 12px;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 3;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    height: 46%;
    margin: 2px 0;
}
::v-deep .mint-navbar .mint-tab-item.is-selected {
    color: #ff7d41;
    border-bottom: 3px solid #ff7d41;
}
</style>



